<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基础信息" name="first">
                <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" @onBasePeople="handleQueryBasePeople" @onPosition="handlePosition"></techo-people>
            </el-tab-pane>
            <el-tab-pane label="位置信息" name="second">
                <vPeoplePosition ref="vPeoplePosition" :xingzhengqu="dic['行政区划']" :disCodeOption="disCodeOption" :isAdd="isAdd"></vPeoplePosition>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="third">    
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
                    <el-row :gutter="15">
                      <el-col :span="6">
                        <el-form-item label="监护人姓名" prop="guardianName">
                            <el-input v-model="dialogForm.guardianName" :maxlength="18" placeholder="请填写监护人姓名"></el-input>
                        </el-form-item>
                        <el-form-item label="家庭经济状况">
                            <el-select placeholder="请选择家庭经济状况" clearable v-model="dialogForm.financialSituation">
                                <el-option v-for="(item, index) in dic['经济状况']" :label="item.name" :value="item.code" :key="index"></el-option>
                            </el-select>
                        </el-form-item>
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="监护人联系方式" prop="guardianContact">
                            <el-input v-model="dialogForm.guardianContact" :maxlength="18" placeholder="请填写联系方式"></el-input>
                        </el-form-item>
                        <el-form-item label="是否纳入低保">
                            <el-radio v-model="dialogForm.isSecurity" :label="1">是</el-radio>
                            <el-radio v-model="dialogForm.isSecurity" :label="0">否</el-radio>
                        </el-form-item>
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="监护人身份证号码" prop="guardianIdCardNo" label-width="160px">
                            <el-input v-model="dialogForm.guardianIdCardNo" :maxlength="18" placeholder="请填写监护人身份证号码"></el-input>
                        </el-form-item>
                        <el-form-item label="目前危险性评估等级" label-width="160px">
                            <el-select placeholder="请选择目前危险性评估等级" clearable v-model="dialogForm.dangerousGrade">
                                <el-option v-for="(item, index) in dic['危险性评估']" :label="item.name" :value="item.code" :key="index"></el-option>
                            </el-select>
                        </el-form-item>
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="初次发病日期">
                            <el-date-picker v-model="dialogForm.diseaseDate" type="date" placeholder="请选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="目前诊断类型">
                            <el-select placeholder="请选择目前诊断类型" clearable v-model="dialogForm.diagnosticType">
                                <el-option v-for="(item, index) in dic['诊断类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                            </el-select>
                        </el-form-item>
                      </el-col>
                    </el-row>
                    <div class="cut-apart-line"></div>
                    <el-row :gutter="15">
                      <el-col :span="6">
                        <el-form-item label="有无肇事肇祸史">
                            <el-radio v-model="dialogForm.isaccident" :label="1">有</el-radio>
                            <el-radio v-model="dialogForm.isaccident" :label="0">无</el-radio>
                        </el-form-item>
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="上次肇事祸日期" v-if="dialogForm.isaccident == 1" prop="accidentLasttime">
                            <el-date-picker v-model="dialogForm.accidentLasttime" type="date" placeholder="请选择日期"></el-date-picker>
                        </el-form-item> 
                      </el-col>
                      <el-col :span="6">
                        <el-form-item label="肇事肇祸次数" v-if="dialogForm.isaccident == 1" prop="accidentFrequency" label-width="160px">
                            <el-input v-model="dialogForm.accidentFrequency" :maxlength="3" placeholder="请填写肇事肇祸次数"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="6"></el-col>
                    </el-row>
                    <div class="cut-apart-line"></div>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="治疗医院名称">
                                <el-input v-model="dialogForm.treatmentHospitalName" :maxlength="25" placeholder="请填写治疗医院名称"></el-input>
                            </el-form-item>
                            <el-form-item label="参与管理人员">
                                <el-select placeholder="请选择参与管理人员" clearable multiple v-model="dialogForm.manager">
                                    <el-option v-for="(item, index) in dic['参与管理人员']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="住院治疗原因">
                                <el-select placeholder="请选择实施住院治疗原因" clearable multiple v-model="dialogForm.treatmentReasons">
                                    <el-option v-for="(item, index) in dic['住院治疗原因']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="帮扶情况">
                                <el-select placeholder="请选择帮扶情况" clearable multiple v-model="dialogForm.helpSituation">
                                    <el-option v-for="(item, index) in dic['帮扶情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="接受康复训练机构名称" label-width="160px">
                                <el-input v-model="dialogForm.recoveryName" :maxlength="25" placeholder="请填写接受康复训练机构名称"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="治疗情况">
                                <el-select placeholder="请选择治疗情况" clearable v-model="dialogForm.treatmentSituation">
                                    <el-option v-for="(item, index) in dic['治疗情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
			<el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/special_people_service.js";
import vPeoplePosition from "../infact_peoples/components/PeoplePosition.vue";

export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  components: {
    vPeoplePosition
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        经济状况: [],
        诊断类型: [],
        危险性评估: [],
        治疗情况: [],
        住院治疗原因: [],
        参与管理人员: [],
        帮扶情况: [],
        行政区划: []
      },
      disCodeOption: [],
      houseOptions: [],
      dialogForm: {
        isSecurity: 1,
        isaccident: 1,
        dangerousGrade: "01",
        diagnosticType: "01",
        treatmentSituation: "01",
        treatmentReasons: ["99"],
        manager: ["01","05"],
        helpSituation: ["01","04"]
      },
      teamRules: {
        guardianIdCardNo: [util.validate.isIDCard()],
        guardianName: [
          { required: true, message: "请输入监护人姓名", trigger: "blur" },
          { min: 2, max: 15, message: "长度在 2 到 15 个字符", trigger: "blur" }
        ],
        guardianContact: [
          { required: true, message: "请输入监护人联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        accidentFrequency: [
            { required: true, message: "请输入肇祸次数 ", trigger: "blur" },
            util.validate.valiInteger()
        ],
        accidentLasttime: [
          { required: true, message: "请选择上次肇祸日期", trigger: "change" }
        ]
      },
      houseInfo: {}
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].baseForm.id &&
          service.requestBaseIdByPeople(
            "people/wrecker/findPage",
            this.$refs["vBaseForm"].baseForm.id,
            data => {
              if (data.totalCount != 0) {
                this.isAdd = false;
                this.dialogForm = data.list[0];
              }
            }
          );
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
        this.$refs["vPeoplePosition"].$refs["houseItem"] &&
          this.$refs["vPeoplePosition"].$refs["houseItem"].initGridData();
        this.$refs["vPeoplePosition"].$refs["liveHouseItem"] &&
          this.$refs["vPeoplePosition"].$refs["liveHouseItem"].initGridData();
      } else if (name == "third") {
        new Promise((resolve, reject) => {
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            this.$refs["vPeoplePosition"].$refs["houseItem"].$refs[
              "houseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
            this.houseInfo = this.$refs["vPeoplePosition"].$refs[
              "houseItem"
            ].houseInfo;
            resolve();  
          } else {
            service.requestHouseInfo(
              this.$refs["vBaseForm"].baseForm.houseId,
              data => {
                this.houseInfo = data;
                resolve();  
              }
            );
          };
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            this.$refs["vPeoplePosition"].$refs["liveHouseItem"].$refs[
              "liveHouseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
          };
        }).then(() => {
          this.dialogForm = {
            ...this.dialogForm,
            districtCode: this.houseInfo.districtCode
          };
        });
      }
    },
    // 有无肇祸是
    "dialogForm.isaccident"(value) {
        if(value == 0) {
            this.dialogForm.accidentLasttime = "";
            this.dialogForm.accidentFrequency = "";
        }
    },
  },

  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 身份证号选择后回填位置信息 */
    handlePosition(item) {
      this.$refs["vPeoplePosition"].hujidi = item.domicilePlaceDetail;
      this.$refs["vPeoplePosition"].xianzhudi = item.livePlaceDetail;
      this.$refs["vPeoplePosition"].showHujiForm = false;
      this.$refs["vPeoplePosition"].showXianzhuForm = false;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate =
            baseInfo.birthDate &&
            util.time.formatDate(baseInfo.birthDate, "YYYY-MM-DD");
          baseInfo.occupationType = _.last(baseInfo.occupationType);
          baseInfo.districtCode = this.houseInfo.districtCode;
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            let houseAddress = this.$refs["vPeoplePosition"].$refs["houseItem"]
              .positionForm;
            baseInfo.domicilePlace = houseAddress.province; // 户籍地
            baseInfo.domicilePlaceDetail = houseAddress.domicilePlaceDetail; // 户籍地详址
            baseInfo.houseId = houseAddress.houseId; // 户籍地户ID
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            let liveAddress = this.$refs["vPeoplePosition"].$refs[
              "liveHouseItem"
            ].positionForm;
            baseInfo.livePlace = liveAddress.province; // 居住地
            baseInfo.livePlaceDetail = liveAddress.domicilePlaceDetail; // 居住地详址
            baseInfo.liveHouseId = liveAddress.houseId || ""; // 居住地户ID
          }
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      otherInfo.diseaseDate =
        otherInfo.diseaseDate &&
        util.time.formatDate(otherInfo.diseaseDate, "YYYY-MM-DD");
      otherInfo.accidentLasttime =
        otherInfo.accidentLasttime &&
        util.time.formatDate(otherInfo.accidentLasttime, "YYYY-MM-DD");
      otherInfo.treatmentReasons = otherInfo.treatmentReasons.join(",");
      otherInfo.manager = otherInfo.manager.join(",");
      otherInfo.helpSituation = otherInfo.helpSituation.join(",");

      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 精神病附加信息
        service.requestSavePsychoticPeople(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "精神病患者添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改
        let param = _.pick(otherInfo, [
          "accidentFrequency",
          "accidentLasttime",
          "dangerousGrade",
          "diagnosticType",
          "diseaseDate",
          "districtCode",
          "financialSituation",
          "guardianContact",
          "guardianIdCardNo",
          "guardianName",
          "helpSituation",
          "id",
          "isSecurity",
          "isaccident",
          "manager",
          "peopleId",
          "recoveryName",
          "treatmentHospitalName",
          "treatmentReasons",
          "treatmentSituation"
        ]);
        service.requestUpdatePsychoticPeople(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "精神病患者修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["vBaseForm"].resetBaseForm();
      this.$refs["dialogForm"].resetFields();
      this.$refs["vPeoplePosition"].destroyedForm();
      this.dialogForm = {
        isSecurity: 1,
        isaccident: 1,
        dangerousGrade: "01",
        diagnosticType: "01",
        treatmentSituation: "01",
        treatmentReasons: ["99"],
        manager: ["01","05"],
        helpSituation: ["01","04"]
      };
      this.houseInfo = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
    }
  }
};
</script>
<style lang="scss">
.add-dialog {
  .el-date-editor.el-input {
    width: 100%;
  }
  .el-select {
    width: 100%;
  }
  .el-cascader {
    width: 100%;
  }
  .cut-apart-line {
    width: 100%;
    border-top: 1px solid #028aff;
    margin-bottom: 22px;
  }
}
</style>